export default {
	name: 'temHome',
	props:{
		msg: {
			type: String,
			default: ''
		},
		type: {
			type: String,
			default: ''
		},
		fromm: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			qrCode: '',
			qrCodeType:'',
			laizi:'',
			xfData: [],
			imgrow: [],
			xgData: [],
			HYid: '',
			PlaceNo: '',
			lon: '',
			lat: '',
			commitTime: 0,
			isDanger: true,
			dec: '',
			position: ''
		};
	},
	onLoad() {
		
	},
	mounted() {
		this.qrCode = this._props.msg;
		this.qrCodeType = this._props.type;
		this.laizi = this._props.fromm;
		this.getData();
		this.$emit('hideLoading',0.1);
		this.getLocation()
		console.log(this.laizi)
	},
	methods:{
		SwitchA(e) {
			for (let name of this.xfData) {
				if (name.Name == e.currentTarget.dataset.title) {
					for (let word of name.Detail) {
						if (word.content == e.currentTarget.dataset.info.content) {
							word.isCheck = e.detail.value;
						}
					}
				}
			}
			console.log(this.xfData)
		},
		showModel(title) {
			uni.showModal({
			    title: '提示',
			    content: title,
					showCancel: false,
			    success: function (res) {
			        if (res.confirm) {
			            
			        }
			    }
			});
		},
		getData() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/School/Get_HD',
				data: {
					QRCode: self.qrCode,
					type: self.qrCodeType
				},
			}).then(data => {
				// console.log(data)
				var [error, res] = data;
				console.log(res.data)
				if (res.data.code == 0) {
					if (self.qrCodeType == 'XF') {
						self.xfData = res.data.data[0].Detail;
						self.HYid = res.data.data[0].ID;
						self.PlaceNo = res.data.data[0].PlaceNo;
						console.log(self.xfData)
						for (let s of self.xfData) {
							if (s.Detail[s.Detail.length-1] == ';') {
								s.Detail = s.Detail.substr(0,s.Detail.length-1);
							}
							let word = s.Detail.split(";");
							s.Detail = [];
							for (let cont of word) {
								s.Detail.push({
									isCheck: false,
									content: cont
								})
							}
						}
						
					}
				} else {
					uni.showToast({
					    title: '扫描失败',
					    duration: 2000,
							icon: 'loading'
					});
				}
			})
		},
		
		
		//图片相关
		ChooseImage() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['camera'], //从相册选择
				success: (res) => {
					console.log(res)
					this.imgrow.push(res.tempFilePaths[0])
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgrow,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			console.log(e)
			uni.showModal({
				title: '删除',
				content: '确定要删除这张图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgrow.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		
		getLocation() {
			var self = this;
			uni.getLocation({
				type: 'gcj02',
				success: function(res) {
					self.lon = res.longitude;
					self.lat = res.latitude;
				}
			});
		},
		commitXf() {
			var len = this.imgrow.length;
			let pcyd = [];
			for (let site of this.xfData) {
				for (let foo of site.Detail) {
					if (foo.isCheck == false) {
						pcyd.push({
							content: foo.content,
							title: site.Name
						})
					}
				}
			}
			
			let pcydWord = '';
			for (let s of pcyd) {
				pcydWord = pcydWord + `${s.title}:${s.content}.`
			}
			console.log(pcydWord);
			if (this.imgrow.length == 0) {
				this.showModel('请拍摄照片！')
			} else if (!this.position) {
				this.showModel('请输入检查记录！')
			}else {
				uni.showLoading({
					title:'提交中。。。',
					mask:true
				})
				this.dec = pcydWord
				if (!pcydWord) {
					this.isDanger = false;
				} else {
					this.isDanger = true;
				}
				this.uploadSafeImg(len);
			}
			
			
		},
		//来源为  消防检查提交
		safeCommit() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/School/ScanQRCode_Check_Safe',
				data: {
					type: self.qrCodeType,
					RYXH: self.$store.state.userInfo.RYXH,
					PlaceNo: self.PlaceNo
				},
			}).then(data => {
				var [error, res] = data;
				uni.hideLoading()
				if (res.data.code == 0) {
					uni.showModal({
						title: '提示',
						content: '提交成功！',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else if (res.data.code == 1){
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel:false,
						confirmText: '确定',
						success: res => {
							uni.reLaunch({
								url: '/pages/basic/home/home'
							})
						}
					})
				} else {
					uni.showModal({
						title: '提示',
						content: '上传失败',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							uni.reLaunch({
								url: '/pages/basic/home/home'
							})
						}
					})
				}
			})
		},
		dangerCommit() {
			var self = this;
			var hdDetailData = [];
			hdDetailData.push({
				HDID: self.HYid,
				CZWT: self.dec,
				position: self.position
			})
			hdDetailData = JSON.stringify(hdDetailData)
			uni.request({
				url: self.$store.state.urlPrefix + '/School/ScanQRCode_Check_Danger',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					type: self.qrCodeType,
					RYXH: self.$store.state.userInfo.RYXH,
					PlaceNo: self.PlaceNo,
					hdDetail: hdDetailData,
					lat: self.lat,
					lon: self.lon
				},
			}).then(data => {
				var [error, res] = data;
				uni.hideLoading()
				if (res.data.code == 0) {
					uni.showModal({
						title: '提示',
						content: '提交成功！',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else if (res.data.code == 1){
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else {
					uni.showModal({
						title: '提示',
						content: '上传失败',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				}
			})
		},
		
		uploadSafeImg(len) {
			var self = this;
			var postImgUrl = self.$store.state.urlPrefix + '/Process/Everyone_safe_images2';
			uni.uploadFile({
				url: postImgUrl,
				filePath: self.imgrow[self.commitTime],
				name: 'image',
				formData: {
					RYXH: self.$store.state.userInfo.RYXH,
					HDID: self.HYid,
				},
				success(res) {
					self.commitTime = self.commitTime + 1;
					if (res.data == 'ok') {
						if (self.commitTime<len) {
							self.uploadSafeImg(len)
						} else {
							//commit words
							self.commitTime = 0;
							if (self.isDanger) {
								if (self.fromm == '隐患上报') {
									self.HYdangerCommit();
								} else {
									self.dangerCommit()
								}
							} else {
								if (self.fromm == '隐患上报') {
									self.HYsafeCommit()();
								} else {
									self.safeCommit();
								}
							}
						}
					} else {
						self.commitTime = 0;
						uni.hideLoading();
						uni.showModal({
							title: '提示',
							content: '上报失败！',
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									uni.reLaunch({
										url: '/pages/basic/home/home'
									})
								}
							}
						});
					}
				}
			})
		},
		
		//来自隐患上报
		
		HYdangerCommit() {
			var self = this;
			var hdDetailData = [];
			hdDetailData.push({
				HDID: self.HYid,
				CZWT: self.dec,
				position: self.position
			})
			hdDetailData = JSON.stringify(hdDetailData)
			uni.request({
				url: self.$store.state.urlPrefix + '/school/ScanQRCode_report',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					RYXH: self.$store.state.userInfo.RYXH,
					hdDetail: hdDetailData,
					lat: self.lat,
					lon: self.lon
				},
			}).then(data => {
				var [error, res] = data;
				uni.hideLoading()
				if (res.data.code == 0) {
					uni.showModal({
						title: '提示',
						content: '提交成功！',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else if (res.data.code == 1){
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else {
					uni.showModal({
						title: '提示',
						content: '上传失败',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				}
			})
		},
		
		
		HYsafeCommit() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/school/ScanQRCode_report_safe',
				data: {
					
					RYXH: self.$store.state.userInfo.RYXH,
					HDIDs: self.HYid,
					position: self.position,
					lat: self.lat,
					lon: self.lon
				},
			}).then(data => {
				var [error, res] = data;
				uni.hideLoading()
				if (res.data.code == 0) {
					uni.showModal({
						title: '提示',
						content: '提交成功！',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							if (res.confirm) {
								uni.reLaunch({
									url: '/pages/basic/home/home'
								})
							}
						}
					})
				} else if (res.data.code == 1){
					uni.showModal({
						title: '提示',
						content: res.data.msg,
						showCancel:false,
						confirmText: '确定',
						success: res => {
							uni.reLaunch({
								url: '/pages/basic/home/home'
							})
						}
					})
				} else {
					uni.showModal({
						title: '提示',
						content: '上传失败',
						showCancel:false,
						confirmText: '确定',
						success: res => {
							uni.reLaunch({
								url: '/pages/basic/home/home'
							})
						}
					})
				}
			})
		},
	}
}
